import React from "react";
import PostCard from "@/components/common/PostCard";
import Header from "@/components/layout/Header";
import Footer from "@/components/layout/Footer";

const PostsPage: React.FC = () => {
  return (
    <>
      <Header />
      <main className="p-8 grid grid-cols-1 md:grid-cols-3 gap-6">
        <PostCard title="Post 1" description="This is the first post." />
        <PostCard title="Post 2" description="This is the second post." />
        <PostCard title="Post 3" description="This is the third post." />
      </main>
      <Footer />
    </>
  );
};

export default PostsPage;
